/* FONT FACE */
.PublicMap-map {
  position: relative;
  width: 100%;
  height: 400px;
  background: #333 url("/assets/1.0.0-assets.136/images/layout/noise.png") repeat 0 0; }

.PublicMap-map.js-map::after {
  content: '';
  display: block;
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 1px;
  background: rgba(0, 0, 0, 0.25); }

.PublicMap-block {
  display: block;
  overflow: hidden; }

.PublicMap-block.with-padding {
  padding-top: 40px; }

.PublicMap-navmenu {
  position: relative;
  z-index: 30; }

.PublicMap-navmenu--owner {
  position: relative;
  z-index: 10; }

.PublicMap-leftBlock {
  width: 40%;
  float: left; }

.PublicMap-gradient {
  content: '';
  display: none;
  position: absolute;
  z-index: 20;
  right: 0;
  width: 60%;
  height: 100%;
  background-image: linear-gradient(to right, rgba(255, 255, 255, 0) 5%, #FFF 10%, #FFF 100%); }

.PublicMap-leftBlock.PublicMap-leftBlock--large {
  width: 100%;
  max-width: 780px; }

.PublicMap-rightBlock {
  width: 60%;
  float: right; }

.PublicMap-description p {
  font-size: 20px;
  font-weight: 300;
  line-height: 32px; }

.PublicMap-secondary {
  background: #F9F9F9; }

.PublicMap-secondary,
.PublicMap-info {
  padding: 60px 0; }

.PublicMap-secondary--bottom {
  padding-bottom: 0; }

.PublicMap-title,
.PublicMap-description {
  margin-bottom: 40px;
  color: #333; }

.PublicMap-meta {
  color: #AAA;
  font-size: 15px; }

.PublicMap-metaList {
  display: inline-block;
  margin-right: 30px; }

.PublicMap-metaItem {
  display: inline-block;
  max-width: 165px;
  margin-right: 30px; }
  @media (max-width: 960px) {
    .PublicMap-metaItem {
      max-width: 100%; } }

.PublicMap-metaItem,
.PublicMap-metaItem p {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap; }

.PublicMap-metaItem p,
.PublicMap-metaItem i {
  display: inline-block;
  max-width: 135px;
  vertical-align: middle; }
  @media (max-width: 960px) {
    .PublicMap-metaItem p,
    .PublicMap-metaItem i {
      max-width: 100%; } }

.PublicMap-metaItem:last-child {
  margin-right: 0; }

.PublicMap-metaLink {
  transition: all 0.2s;
  color: #979ea1;
  text-decoration: none; }

.PublicMap-metaLink:hover {
  color: #2e3c43;
  text-decoration: none; }

.PublicMap-metaIcon {
  margin-right: 12px; }

.PublicMap-secondaryTitle {
  padding: 20px 0;
  border-bottom: 1px solid #DDD;
  color: #333333;
  font-size: 13px;
  font-weight: 500;
  line-height: normal;
  text-align: left;
  text-transform: uppercase; }

.PublicMap-privateDatasets {
  margin-left: 20px;
  color: #999999;
  line-height: 40px; }

.PublicMap-mapsList {
  padding-top: 10px; }

@media (min-width: 961px) {
  .PublicMap-metaList.PublicMap-metaList--mobile {
    display: inline-block !important; } }

@media (max-width: 960px) {
  .PublicMap-gradient {
    display: block; }
  .PublicMap-leftBlock.PublicMap-leftBlock--owner {
    position: relative;
    z-index: 10;
    width: 100%; }
  .PublicMap-rightBlock.PublicMap-rightBlock--owner {
    position: absolute;
    z-index: 30;
    right: 15px;
    width: 60%; }
  .PublicMap-metaList {
    display: block;
    width: 100%; }
  .PublicMap-metaList + .PublicMap-metaList {
    border-top: 1px solid #DDD; }
  .PublicMap-metaItem {
    width: 100%;
    padding: 20px 0; }
  .PublicMap-metaList.PublicMap-metaList--mobile {
    display: none; }
  .PublicMap-metaList.PublicMap-metaList--mobile .PublicMap-metaItem {
    border-top: 1px solid #DDD; }
  .PublicMap-metaList.PublicMap-metaList--mobile .PublicMap-metaItem:first-child {
    border-top: 0; }
  .DatasetsList-itemMeta {
    float: right; } }
