.PublicButton {
  display: inline-block;
  transition: all 0.3s;
  border-width: 2px;
  border-style: solid;
  border-radius: 4px;
  outline: none;
  font-size: 12px;
  font-weight: 600;
  line-height: 1;
  text-align: center;
  white-space: nowrap; }
  .PublicButton:hover {
    text-decoration: none; }
  .PublicButton > span {
    display: inline-block; }

.PublicButton--responsive {
  width: 100%; }
  .PublicButton--responsive > span {
    max-width: 100%; }

.PublicButton--long > span {
  min-width: 140px; }

.PublicButton--sSmall > span {
  padding: 8px 20px; }

.PublicButton--sMedium > span {
  padding: 10px 20px; }

.PublicButton--sLarge > span {
  padding: 12px 20px; }

.PublicButton--sXlarge > span {
  padding: 14px 40px; }

.PublicButton--cWhite {
  border-color: rgba(255, 255, 255, 0.3);
  color: #FFF; }
  .PublicButton--cWhite:hover {
    border-color: rgba(255, 255, 255, 0.4); }
  .PublicButton--cWhite.is-highlight {
    background: #FFF;
    color: #3AA9E3; }
    .PublicButton--cWhite.is-highlight:hover {
      opacity: 0.9; }
  .PublicButton--cWhite.is-hover:hover {
    background: #FFF;
    color: #3AA9E3; }
  .PublicButton--cWhite.is-hover--dark:hover {
    color: #2E3C43; }

.PublicButton--cBlue {
  border-color: rgba(58, 169, 227, 0.3);
  color: #3AA9E3; }
  .PublicButton--cBlue:hover {
    border-color: rgba(58, 169, 227, 0.4); }
  .PublicButton--cBlue.is-highlight {
    background: #3AA9E3;
    color: #FFF; }
    .PublicButton--cBlue.is-highlight:hover {
      opacity: 0.9; }
  .PublicButton--cBlue.is-hover:hover {
    background: #3AA9E3;
    color: #FFF; }
  .PublicButton--cBlue.is-hover--dark:hover {
    color: #2E3C43; }
  .PublicButton--cBlue.is-highlight--fb {
    border-color: #3A5999;
    background: #3A5999; }
    .PublicButton--cBlue.is-highlight--fb:hover {
      border-color: #334e87;
      background: #334e87; }
  .PublicButton--cBlue.is-highlight--tw {
    border: 2px solid #55ACEE;
    background: #55ACEE; }
    .PublicButton--cBlue.is-highlight--tw:hover {
      border-color: #3ea1ec;
      background: #3ea1ec; }
  .PublicButton--cBlue.is-highlight--li {
    border: 2px solid #007BB6;
    background: #007BB6; }
    .PublicButton--cBlue.is-highlight--li:hover {
      border-color: #006a9d;
      background: #006a9d; }

.PublicButton--public {
  font-size: 13px;
  text-transform: none; }
